.navbar-menu {
  background-color: #fafafa;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  transform: translateY(-100%);
  transition: 0.5s; }

.navbar-menu.active {
  transform: translateY(0);
  transition: 0.5s; }

.navbar-menu .menu-listing {
  padding: 0;
  margin-top: 10vh;
  text-align: right; }

.menu-listing li {
  display: block;
  margin: 16px; }

.menu-listing li a {
  color: #262626;
  display: block;
  font-size: 2rem;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  letter-spacing: 1px;
  text-decoration: none;
  transition: 0.5s; }

.menu-listing li a:active {
  color: #fff;
  transition: 0.5s; }

.menu {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 54px;
  width: 54px;
  cursor: pointer;
  transition: 0.3s;
  z-index: 9999; }

.menu span {
  position: absolute;
  height: 3px;
  width: 25px;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s; }

.menu span:before {
  content: '';
  position: absolute;
  top: -8px;
  background-color: #fff;
  height: 3px;
  width: 25px;
  transition: 0.3s; }

.menu span:after {
  content: '';
  position: absolute;
  top: 8px;
  background-color: #fff;
  height: 3px;
  width: 25px;
  transition: 0.3s; }

.menu.active span {
  background-color: transparent; }

.menu.active span:before {
  background-color: white;
  transform: rotate(45deg);
  top: 0; }

.menu.active span:after {
  background-color: white;
  transform: rotate(-45deg);
  top: 0; }

.navbar-menu-md {
  display: none; }

@media (min-width: 900px) {
  .navbar-menu-md {
    display: inline-block;
    position: absolute;
    width: 100%; }
    .navbar-menu-md .menu-listing li a {
      font-size: 1em;
      color: white; }
    .navbar-menu-md .menu-listing li {
      display: inline-block;
      background-color: transparent; }
  .menu {
    display: none; } }
